.facts {
  min-height: 503px;
  padding-top: 150px;
  text-align: center;
}

.facts__header {
  margin: 0;
  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
}

.facts__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 50px 117px 0 117px;
  font-size: 22px;
  line-height: 34px;
}

.facts__item {
  padding: 0 20px;
}

.facts__number {
  font-weight: bold;
  font-size: 28px;
  line-height: 38px;
  color: $main_color;
}

.facts__descr {
  padding-top: 14px;
}
